$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.container {
    background-color: #ffffff;
    // padding: 64px;
    flex: 1;
    display: flex;

    .content {
        flex: 1;
        display: flex;
    //     max-width: 1200px;
    // margin: auto;
    }

    .main {
        flex: 1;
        background-image: url(./images/startup.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        background-color: rgb(236, 239, 241);
    }

    // .email_login {
    //     width: 400px;
    //     margin: auto;
    // }

    .login {
        // padding-top: 64px;
        display: flex;
        justify-content: center;
        

        &-body {
            margin-top: 96px;
            width: 400px;
            padding: 24px;
        }

        &-title {
            text-align: center;
            margin-bottom: 32px;
        }

        &-btn {
            margin-top: 24px;
        }
    }

    .registry_success {
        text-align: center;
    }
}